<template>
    <div>
        <el-menu :default-active="$route.name" class="el-menu-vertical-demo" @select="handleSelectMenu"
            background-color="#324157" text-color="#fff">

            <div v-for="(menu, idx) in aMenuList">
                <el-menu-item v-if="menu.clildrenMenu == null && menu.isMenu" :key="idx" :index="'/' + menu.name">
                    <span slot="title">{{ menu.title }}</span>
                </el-menu-item>
                <el-submenu v-if="menu.clildrenMenu != null" :index="menu.name">
                    <span slot="title">{{ menu.title }}</span>
                    <el-menu-item v-for="(child, idx) in menu.clildrenMenu" :key="idx" :index="'/' + menu.name + '/' + child.name">{{ child.title }}</el-menu-item>
                </el-submenu>

            </div>
        </el-menu>
    </div>
</template>

<script>
export default {
    name: 'sideMenu',
    components: {},
    props: {},
    data() {
        return {}
    },
    computed: {
        aMenuList() {
            return this.$store.state.app.aMenuList
        }
    },
    methods: {
        //点击菜单
        handleSelectMenu(params) {
            this.$router.push(params)
        },
    },
    mounted() { }
}
</script>

