<template>
<div class="app-wrapper">
    <div class="sidebar-container">
        <logo class="sidebar-logo-container" />
        <side-menu />
    </div>
    <div class="main-container">
        <div class="header-container">
            <div class="navbar">
                <div class="nav-right">
                    <user class="user-con" :username="username" />
                    <i class="el-icon-switch-button logout-btn" @click="handleLogout"></i>
                </div>
            </div>
        </div>
        <app-main />
    </div>
</div>
</template>

<script>
import logo from "./components/logo";
import sideMenu from "./components/sideMenu";
import user from "./components/user";
import appMain from "./components/appMain";
export default {
    name: "app-wrapper",
    components: {
        logo,
        sideMenu,
        user,
        appMain
    },
    props: {},
    data() {
        return {};
    },
    computed: {
        username() {
            let userInfo = JSON.parse(sessionStorage.getItem("userInfo"));
            let powerTypeMap = [
                "普通用户",
                "超级管理员",
                "配置设置管理员",
                "后台客服",
                "前台客服"
            ];
            let show = ""
            if (userInfo.powerTypeName != null){
                show = userInfo.powerTypeName + ":" + userInfo.name;
            }else {
                show = powerTypeMap[userInfo.powerType] + ":" + userInfo.name;
            }
            return show;
        }

    },
    methods: {
        handleLogout() {
            this.$confirm("此操作将退出登录状态, 是否继续?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            }).then(() => {
                this.$fetch.post(API.app.logout).then(response => {
                    let res = response.data;
                    if (res.status == 200) {
                        sessionStorage.removeItem("status");
                        sessionStorage.removeItem("userInfo");
                        this.$router.push({
                            name: "login"
                        });
                    } else {
                        this.$message.error(res.desc);
                    }
                });
                sessionStorage.removeItem("status");
                this.$router.push({
                    name: "login"
                });
            });

        }
    },
    mounted() {
        //
    }
};
</script>

<style lang="stylus">
@import './layout.styl';
</style>
